.details {
    padding: 20px;
    width: 100%;
    position: fixed;
    z-index: 999;
    background-color: var(--white);
    bottom: 0;
    left: 0;

    &__header {
        gap: 15px;
    }

    &__close {
        width: 20px;
        height: 20px;
    }

    &__icon-section {
        border-top: 1px solid var(--3-gray);
        border-bottom: 1px solid var(--3-gray);
        margin: 16px 0px;
        width: 100%;
        height: 55px;
        gap: 32px;
        padding: 40px 0;
        padding-left: 16px;
    }

    &__available {
        gap: 25px;
        margin-top:6px;

        &__report-btn {
            margin-left: 10px;
            border: 1px solid var(--5-gray);
            border-radius: 15px;
            height: 25px;
        }
    }
    &__detail-section{
        font-size: 14px;
    }

    &__footer {
        text-align: center;
    }
}

.details__icon-section__icon {
    width: 30px;
    height: 30px;
}

@media(min-width:750px) {
    .details {
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        width: 60%;


    }
}

@media(min-width:1024px) {
    .details {
        &__header {
            font-size: 24px;
        }

        &__available {
            margin-top: 10px;
            font-size: 20px;

        
        }

        &__icon-section {
            font-size: 20px;
        }
        &__detail-section{
            font-size: 16px;
        }

    }
}